#header {
    position: fixed;
    left: 0px;
    top: 0px;

    width: 100%;

    display: flex;
    flex-direction: row;
    align-items: center;

    padding: 15px;

    background-color: white;

    z-index: 11;
}

#header ion-icon {
    width: 30px;
    height: 30px;

    color: #383838;
}

#header .hamburger {
    padding: 0px;
    transform: scale(0.6);
}

@media (min-width: 1000px) {
    #header {
        display: none;
    }
}